.App {
    border-top: 25px solid ;
    border-top-color: #85144b;
    border-top-width:100;
  }
  .header {
      text-align: center;
  }
  
.pokeball {
    width: 60px;
}

h1 {
    color: #85144b;
    text-align: center;
}
.search-container{
    margin-top: 20px;
    margin-bottom: 20px;
}
.Search-box{
position: absolute;
top:50%;
left:50%;
-webkit-transform: translate(-50%,-50%);
        transform: translate(-50%,-50%);
background: #85144b;
height: 40px;
border-radius: 40px;
padding: 10px;
}

.Search-box:hover > .searchText{
    width:240px;
    padding: 0 6px;
}

.searchButton{
    float: right;
    display: flex; 
    justify-content: inherit;
    align-items: initial;
}
.searchText{
    border: none;
    background: none;
    outline: none;
    padding: 0;
    font-size: 16px;
    transition: 0.4s;
    line-height: 40px;
    width: 0px;
}


.pokemon-container{
    margin-top: 50px;

}
.pokemon-link{

    border-radius: 25px;
    border: 1px solid gray;
    margin: 10px;
}
.pokemon-link:hover{
    font-size: 150%;
}
.poke-name{
    color: blue;
    align-content: left;
    cursor: pointer;
}
.profile {
    margin-top: 50px;
}
.pokemon-name{
    justify-content: right
}
.poke-avatar{
    margin-top:15px;
    margin-left: 40px;
    /* top: -60px; */
    width: 120px;
    height: 100px;
}
.pokemon-stats{
    margin-left: 80px;
    margin-right: 80px;
    border: 1px solid gray;
    max-inline-size: 100%;
    border-radius: 1px;

}
.poke-moves {
    flex-wrap: nowrap;
    overflow-x:auto;
    padding-left: 25px;
}
.move {
    margin: 5px;
    font-weight: bolder;
    cursor:pointer;

}
h1 {
    text-align: center
}
.type{
    margin-left: 20px;
}
.pokemon-nums{
    border-left: 1px solid gray
}
